import React from 'react';

class History extends React.Component {
    render() {
        return(
            <div>
                <label className={`emoji ` + this.props.icon}></label>
                {` `}
                <a target="_blank" rel="noopener" href={`https://` + this.props.url}>{this.props.title}</a>
            </div>
        );
    }
}

export default History