import React from 'react';

import photoOne from '../../photos/1.png';
import photoTwo from '../../photos/2.png';
import photoThree from '../../photos/3.png';
import photoFour from '../../photos/4.png';

class Profile extends React.Component {
    render() {
        return(
            <div>
                <section className="profile">
                    <img className="avatar" id="ava" alt="Это Я :)"></img>
                    <h1 className="title">{this.props.name}</h1>
                    <p className="description">{this.props.description}</p>
                </section>
            </div>
        );
    }

    componentDidMount() {
        const avatar = document.getElementById('ava');
        let randNum = Math.floor(Math.random() * 4);

        if (avatar) {
            switch (randNum) {
                case 0:
                    avatar.src = photoOne;
                    console.log('Рандом аватарка: ', randNum);
                break;

                case 1:
                    avatar.src = photoTwo;
                    console.log('Рандом аватарка: ', randNum);
                break;

                case 2:
                    avatar.src = photoThree;
                    console.log('Рандом аватарка: ', randNum);
                break;

                case 3:
                    avatar.src = photoFour;
                    console.log('Рандом аватарка: ', randNum);
                break;
            }
        }

        else {
            console.log('Аватарку сменить нельзя! Что-то пошло не так :(');
        }
    }
}

export default Profile