import React from 'react';
import Section from '../../components/main/Section';

class NotFound extends React.Component {
    render() {
        return(
            <div>
                <article className="markdown-body">
                    <Section
                        title = "Ой!"
                        emoji = "f_face"

                        content = {
                            <p>
                                Страница не найдена.
                                <br/>
                                » <a href="/">Вернуться на главную</a>
                            </p>
                        }
                    />
                </article>
            </div>
        );
    }
}

export default NotFound